import conversionPrice from '@/components/conversionPrice.vue';
import interfaces from '@/utils/interfaces.js';
import { mapState, mapMutations } from 'vuex';
export default {
	computed: mapState(['hasLogin', 'uerInfo']),
	data() {
		return {
			//user_id: '',
			poolfrom: {
				collect_type:1,
			},
			pageSize: 6, //分页大小
			pageNum: 1, //页码
			loadingText: "正在加载....",
			goodsPrice: [],
			current: 0,
		};
	},
	methods: {
		getpoollist() {//收藏
			this.request({
				url: interfaces.getPoolData,
				dataType: 'JSON',
				method: 'POST', //请求方式
				data: {
					data: {
						collect_type: this.poolfrom.collect_type,
						page_size: this.pageSize,
						page_index: this.pageNum
					}
				},
				success: (res) => {
					if (res.code == 200) {
						let lists = res.data.list;
						if (lists.length < this.pageSize) {
							this.loadingText = "到底了";
						}
						if (lists.length > 0) {
							lists.forEach(item => {
								this.goodsPrice.push(item);
							})
						} else {
							this.loadingText = "到底了";
						} 
					}
				}
			});
		},
		// 上拉加载
		onReachBottom() {
			//debugger
		this.pageNum++;
		this.getpoollist();
		}
	},
	onLoad() {
		//this.user_id = this.uerInfo.user_Id;
		// console.log(this.user_id,2222);
		this.getpoollist();
	},
	onReachBottom() {
		this.poolfrom.page_index++;
		this.getpoollist();
	}
};
